@import url("htpps://fonts.googleapis.comcss2?family=Cedarville+Cursive&display=swap");
.login {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100vh;
    background-image: linear-gradient( 132.6deg,  rgba(71,139,214,1) 23.3%, rgba(37,216,211,1) 84.7% );
    
    
}

.login__container {
    display: flex;
    flex-direction: column;
    padding: 20px;
    width: 700px;
    background-color: rgb(255, 255, 255);
    border-radius: 10px;
}

.login__logo {
    display: flex;
    justify-content: center;
    object-fit: contain;
}

.login__logo>img {
    object-fit: contain;
    height: 100px;
}

.login__desc {
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-top: 10px;
}

.login__desc>h3 {
    margin-top: 10px;
    text-align: center;
    font-size: xx-large;
    font-family: "Cedarville Cursive", cursive;
}

.login__desc>p {
    color: gray;
    text-align: center;
    font-weight: 500;
    font-size: 20px;
}

.login__auth {
    display: flex;
    margin-top: 50px;
}

.login__authOptions {
    display: flex;
    flex: 0.5;
    flex-direction: column;
    padding: 20px;
    border-right: 1px solid lightgray;
}

.login__authOption {
    display: flex;
    align-items: center;
    padding: 7px;
    margin-bottom: 15px;
    border: 1px solid lightgray;
    border-radius: 5px;
    cursor: pointer;
}

.login__authOption:hover {
    background-color: rgb(226, 226, 226);
}

.login__authOption>img {
    height: 30px;
    object-fit: contain;
}

.login__authOption>p {
    margin-left: 11px;
}

.login__authDesc {
    display: flex;
    align-items: center;
    padding: 7px;
    margin-bottom: 15px;
}

.login__authDesc>p {
    font-size: small;
    color: gray;
}

.login__emailPass {
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 20px;
    flex: 0.5;
    margin-left: 20px;
}

.login__label>h4 {
    padding: 5px;
}

.login__inputFields {
    display: flex;
    flex-direction: column;
    padding: 5px;
}

.login__inputField {
    padding: 5px;
}

.login__inputField>input {
    padding: 10px;
    width: 100%;
    outline: none;
    border: none;
    background-color: rgb(236, 236, 236);
}

.login__forgButt {
    padding-left: 10px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.login__forgButt>small {
    color: gray;
    cursor: pointer;
}

.login__forgButt>small:hover {
    color: blue;
    text-decoration: underline;
}

.login__forgButt>button {
    padding: 9px;
    border-radius: 5px;
    border: none;
    outline: none;
    background-color: #2a70da;
    color: white;
    font-weight: bold;
    font-size: 15px;
    cursor: pointer;
}

.login__emailPass>button {
    padding: 9px;
    border-radius: 5px;
    border: none;
    outline: none;
    background-color: #2a70da;
    color: white;
    font-weight: bold;
    font-size: 15px;
    cursor: pointer;
    margin-top: 10px;
}

.login__footer {
    padding-top: 10px;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    border-top: 1px solid lightgray;
}

.login__footer>p {
    color: gray;
    font-size: small;
    cursor: pointer;
}

.login__footer>p:hover {
    text-decoration: underline;
}