.wrapper
{
    display:flex;
    flex-direction: column;
    padding: 10px;
    border: 1px solid lightgray;
    background-color: white;
    border-radius: 2px;
    cursor:pointer;
}
.wrapper:hover {
    border:1px solid lightgray;
}

.info
{
    display:flex;
    align-items:center;
}

.info > h5
{
    color:rgb(114, 114, 131);
    font-weight: 300;
    margin-left:10px;
}
.box
{
    display: flex;
    margin-top: 8px;
}
.box > p
{
    font-weight: bold;
    color: grey;
    font-size: large;
}
.modal__title {
    display: flex;
    align-items: center;
    margin-bottom: 5px;
    border-bottom: 1px solid rgba(187, 187, 187, 0.5);
}

.modal__title>h5 {
    color: gray;
    font-size: 20px;
    cursor: pointer;
    font-weight: 500;
    margin-right: 30px;
}

.modal__title>h5:hover {
    color: #16171a;
}

.modal__info {
    display: flex;
    align-items: center;
    margin-top: 30px;
    flex-wrap: wrap;
    align-content: space-between;
    justify-content: space-between;
    flex-direction: row-reverse;

}

.modal__info>p {
    margin-left: 10px;
    font-size: small;
    color: gray;
}

.modal__info>.modal__select>select {
    display: flex;
    align-items: center;
    color: rgb(98, 98, 98);
    padding: 5px;
    margin-left: 10px;
    background-color: rgb(230, 230, 230);
    border-radius: 33px;
    cursor: pointer;
}

.modal__field {
    display: flex;
    flex-direction: column;
    margin-top: 30px;
    flex: 1;
}

.modal__field>.modal__fieldLink {
    color: gray;
    display: flex;
    margin-top: 10px;
    align-items: center;
}

.modal__field>.modal__fieldLink>.modal__link {
    flex: 1;
    border: none;
    outline: none;
    margin-left: 5px;
}

.modal__buttons {
         display: flex;
        flex-direction: column-reverse;
        margin-top: 10px;
        align-items: center;
}
.modal__button>.cancel:hover {
    background-color: #dfdfdf;
    color: black;
}
.modal__buttons>.cancel {
    margin-top: 10px;   
    border: none;
    outline: none;
    color: gray;
    font-weight: 500;
    padding: 10px;
    border-radius: 33px;
    cursor: pointer;
}

.modal__buttons>.add {
    border: none;
    outline: none;
    margin-top: 10px;
    background-color: #1b6be4;
    color: white;
    font-weight: 700;
    padding: 10px px;
    border-radius: 33px;
    width: 200px;
    cursor: pointer;
   
}
.react-responsive-modal-overlay {
    background: rgb(0 0 0 / 75%);
  }

  .react-responsive-modal-modal {
    width: 90vw;
    height: 90vh !important;
  }