.widget
{

    flex: 0.2 1;
    margin-left: 20px;
    display: flex;
    flex-direction: column;
    padding: 10px;
    margin-right: 20px;
    border: 1px solid lightgray;
    border-radius: 5px;
    max-height: 85vh;
    background-color: white;
}

.header {
    border-bottom: 1px solid lightgray;
}

.header>h5 {
    padding: 10px;
    color: rgb(80, 80, 80);
    font-size: 15px;
    letter-spacing: 1.1px;
}

.widget__contents {
    display: flex;
    flex-direction: column;
    cursor: pointer;
}
